#acf-flexible-content-modal-overlay {
  display: none;
}

.edit-post-sidebar, .editor-sidebar {
  .block-editor-rich-text__editable {
    border: 1px solid #949494;
    border-radius: 2px;
    box-shadow: 0 0 0 transparent;
    font-size: 13px;
    line-height: normal;
    padding: 6px 8px;
    transition: box-shadow .1s linear;
    width: 100%;
    min-height: 60px;
    margin-bottom: 30px;
  }
}